<template>
  <div class="essay m">
    <div class="essay-left">
      <div v-if="labels.length>0" class="lables">
        <ul>
          <li class="label" v-for="(item,index) in labels" @click="tolabel(item.name)" :key="index">
            {{item.name}}
          </li>
        </ul>
      </div>
      <div class="article">
        <div class="art-title">{{art.title}}</div>
        <div class="summary">
          {{art.summary}}
        </div>
        <div class="art-img" v-if="art.pic1">
          <img v-if="art.pic1" :src="art.pic1" alt="">
          <img v-if="art.pic2" :src="art.pic2" alt="">
          <img v-if="art.pic3" :src="art.pic3" alt="">
        </div>
        <div class="btns">
          <!--<div class="ans"><img src="@/images/write.png" alt="">回答</div>-->
          <div class="collect" v-if="art.is_collect==0" @click="tocollect()"><img src="@/images/shoucang.png" alt="">收藏
          </div>
          <div class="collect" v-else @click="tocollect()"><img src="@/images/shoucang.png" alt="">已收藏</div>
          <div class="invite" @click="showlist = !showlist"><img src="@/images/adduser.png" alt="">{{showlist?'邀请回答':'收起回答'}}
          </div>
          <div class="share"><img src="@/images/share1.png" alt="">分享
            <div class="share-way">
              <span class="weixin"><img src="@/images/weixin.png" alt="">
              <span class="qr-box">
                <div class="qr">
                  <canvas id="qr"></canvas>
                </div>
              </span>
              </span>
              <span class="weibo" @click="toweibo(art.title,art.aid)"><img src="@/images/weibo.png"
                                                                           alt=""></span>
            </div>
          </div>
        </div>
      </div>
      <div class="invitebar" v-if="!showlist">
        <ul>
          <li v-for="(item,index) in invitelist" :key="index" class="author">
            <div class="user">
              <div class="head" @click="touser(item.uid)"><img :src="item.head" alt=""></div>
              <div class="info" @click="touser(item.uid)">
                <div class="name">{{item.username}}</div>
                <!--<div class="label">青云计划获奖者 优质国际咨询领域创作者</div>-->
              </div>
            </div>
            <div class="btn">
              <el-button size="small" v-if="item.is_invite==1" type="danger">
                <i style="font-size: 12px;margin-right: 10px;" class="iconfont icon-jiaren"></i>已邀请
              </el-button>
              <el-button size="small" v-else type="primary" @click="toinvite(item.uid,item.username)">
                <i style="font-size: 12px;margin-right: 10px;" class="iconfont icon-jiaren"></i>邀请
              </el-button>
            </div>
          </li>
        </ul>
      </div>
      <div class="editor" @click="show=true">
        <editor
          :content="content"
          :path="path"
          v-model="result"
          ref="wangeditor">
        </editor>
        <div class="submit" v-if="show">
          <p class="tips">{{tips}}</p>
          <div class="btns" style="width: 300px;display: flex;justify-content: flex-end">
            <el-button type="primary" size="small" @click="save">保存为草稿</el-button>
            <el-button type="primary" size="small" @click="submit">提交</el-button>
          </div>
        </div>
      </div>
      <!--<el-button type="primary"  size="small" @click="init">清空内容</el-button>-->
      <div class="response">
        <div class="response-num">{{totalnum}}个回答</div>
        <div class="answer">
          <ul>
            <li class="ans-item" v-for="(item,index) in answer" :key="index">
              <div class="user">
                <div class="head" @click="touser(item.uid)">
                  <img :src="item.head" alt="">
                </div>
                <div class="user-info">
                  <div class="top">
                    <div class="name" @click="touser(item.uid)">{{item.username}}</div>
                    <div class="labels" @click="touser(item.uid)">
                      {{item.label}}
                      <!--<ul>-->
                      <!--<li v-for="(el,i) in item.labels" :key="i" class="label">{{el.label}}</li>-->
                      <!--</ul>-->
                    </div>
                  </div>
                  <div class="bottom">
                    {{item.dateline|dayFmt}}
                  </div>
                </div>
              </div>
              <div v-html="item.content" class="user-ans">
              </div>
              <div class="operate">
                <div class="left">
                  <span class="exist" v-if="item.islike==1"><i class="iconfont icondianzan"
                                                               style="color: #409eff; margin-right: 5px;"></i>{{item.likenum}}赞</span>
                  <span v-else @click="tolike(item.aid2,item.subject,item.uid,item.username)"
                        class="praise"><i class="iconfont icondianzan"
                                          style="margin-right: 5px;"></i>{{item.likenum}}赞</span>

                  <!--<span v-if="item.isrubbish==1" class="exist">-->
                  <!--<i class="iconfont iconyijin14-cai" style="color: #409eff; margin-right: 5px;"></i>踩</span>-->
                  <!--<span class="tread" v-else @click="torubbish(item.id,item.subject,item.uid,item.username)"><i-->
                  <!--class="iconfont iconyijin14-cai" style="margin-right: 5px;"></i>踩</span> -->
                  <span class="tread" @click="torubbish(item.id,item.subject,item.uid,item.username)"><i
                    class="iconfont iconyijin14-cai" style="margin-right: 5px;"></i>踩</span>
                  <span class="comment" @click="tocomment(index)"><img src="@/images/comment.png" alt="">{{item.commentnum}}评价</span>
                  <!--<span class="share"> <img src="@/images/share.png" alt="">分享</span>-->
                  <div class="share">
                    <div class="share-word">
                      <img src="@/images/share.png" alt="">分享
                    </div>
                    <div class="share-way">
              <span class="weixin"><img src="@/images/weixin.png" alt="">
              <span class="qr-box">
                <div class="qr">
                  <canvas :id="item.id"></canvas>
                </div>
              </span>
              </span>
                      <span class="weibo" @click="toweibo(item.subject,item.aid)"><img src="@/images/weibo.png"
                                                                                       alt=""></span>
                    </div>
                  </div>
                </div>
                <div class="right">
                  <!--<span class="jubao"><img src="@/images/share.png" alt="">举报</span>-->
                </div>
              </div>
              <!--评论列表-->
              <div class="comments" v-if="item.ablecomment">
                <div class="sendword">
                  <div class="text">
                    <!--  <textarea placeholder="请发表你的看法(不少于5个字)" v-model="item.commentwords" name="word" id="word" cols="30"
                                rows="10"></textarea> -->
                    <!--<textarea :placeholder="'@ '+item.username" v-model="item.commentwords" name="word"-->
                    <!--:id="'word'+item.aid2"-->
                    <!--cols="30"-->
                    <!--rows="10"></textarea>    -->
                    <textarea :placeholder="'@ '+item.username" v-model="message" name="word"
                              :id="'word'+item.aid2"
                              cols="30"
                              rows="10"></textarea>
                  </div>
                  <div class="btn">
                    <!--<el-button type="primary"-->
                    <!--@click="toAnswer(index,item.id,item.uid,item.username,item.catid,item.subject,item.commentwords)"-->
                    <!--size="small">发表评论-->
                    <!--</el-button>  -->
                    <el-button type="primary"
                               :disabled="sendable"
                               @click="toAnswer(index,item.id,item.uid,item.username,item.catid,item.subject,)"
                               size="small">发表评论
                    </el-button>
                  </div>
                </div>
                <div class="comment-list">
                  <ul>
                    <li v-for="(el,i) in item.list" class="list" :key="i">
                      <div class="box">
                        <div class="head" @click="touser(el.uid)">
                          <img :src="el.head" alt="">
                        </div>
                        <div class="info">
                          <div class="top">
                            <div class="name" @click="touser(el.uid)">{{el.username}}</div>
                            <div class="date">{{el.dateline|dayFmt}}</div>
                          </div>
                          <div class="comword">{{el.message}}</div>
                          <div class="opt">
                            <span class="praise" v-if="el.is_like==1">
                              <img src="@/images/praise1.png" style="vertical-align: middle"
                                   alt="">{{el.likenum}}</span>
                            <span class="praise" v-else @click="topraise(el.id,el.title,el.uid,el.username)">
                              <img src="@/images/praise.png" style="vertical-align: middle" alt="">{{el.likenum}}</span>

                            <span class="comment" @click="toAns(el.username,item.id,index)"><img
                              src="@/images/comment.png" alt="">{{el.comment}}</span>
                          </div>
                        </div>
                      </div>
                      <div v-if="el.ablecomment" class="seccomment">
                        <div class="sec-box">
                          <textarea :placeholder="'@'+el.username" name="word" cols="30"
                                    rows="10"></textarea>

                        </div>
                        <div class="respond">
                          <el-button class="resbtn">回复</el-button>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>


            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="essay-right">
      <div class="user" style="margin-bottom: 20px;">
        <keep-alive>
          <userinfo></userinfo>
        </keep-alive>
      </div>
      <keep-alive>
        <recommend></recommend>
      </keep-alive>
      <keep-alive>
        <answer></answer>
      </keep-alive>
    </div>
  </div>
</template>

<script>
  // 引入qrcode
  import QRCode from 'qrcode'
  import {mapState} from 'vuex'

  export default {
    name: "essay",

    data() {
      return {
        art: {},
        aid: '',
        labels: [],
        // input content to editor
        content: '',
        totalnum: 0,
        // output content from editor
        result: '',
        // set image upload api url
        path: 'http://www.yeschains.com/index/Update/upload',
        show: false,
        tips: '分享你的真实观点和经验，通过汇编、洗稿的等方式拼凑的回答将会被折叠',
        answer: [],
        showlist: true,
        invitelist: [],
        acindex: '',
        draft: false,
        message: '',//发表评论
        sendable: false

      }
    },
    watch: {
      message(val) {
        var len = 0;
        for (var i = 0; i < val.length; i++) {
          var a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (len >= 8) {
          this.sendable = false;
        } else {
          this.sendable = true;
        }
      },


      $route() {
        this.aid = this.$route.query.aid;
        this.getcontent();
      },
      aid(val, old) {
        this.getart();
        this.answerList();
        this.getinvite();
      }
    },
    computed: mapState(['isLogin', 'userInfo']),
    mounted() {
      this.init();
      this.getart();
      this.answerList();
      this.getinvite();
      this.getcontent();
      // this.savadraft();
    },
    methods: {
      init() {
        let host = window.location.href;
        this.qr(host, "qr")
      },

      tolabel(name) {
        this.$router.push({path: 'label', query: {label: name}})
      },
      //问题回答
      toAns(name, id, index) {
        // this.answer[index].commentwords = "@" + name;
        this.message = "@" + name;
        let tid = "word" + id;
        let target = document.getElementById(tid).offsetTop - 100;
        this.goTo(target);
        // this.answer[index].commentwords="5465465465456";
        this.answer = this.answer.slice();
      },
      //滚动到指点位置
      goTo(target) {
        window.scrollTo(0, target);
      },
      //获取问题
      getart() {
        this.aid = this.$route.query.aid;
        let aid = this.aid;
        this.$post('index/data/question_one', {aid}).then(res => {
          this.art = res.data;
          var label = [];
          var str = this.art.label.split(",");
          str.forEach((val, index) => {
            var arr = {};
            arr.name = val;
            if (val) {
              label.push(arr);
            }
          });
          this.labels = label.slice(0);
        }).catch(err => {
          console.log(err);
        })
      },
      //用户详情
      touser(uid) {
        this.$router.push({name: 'user', query: {otheruid: uid}})
      },
      //点赞
      tolike(aid, title, uid, username) {
        if (this.isLogin) {
          this.$post('index/data/like', {aid, title, uid, username}).then(res => {
            if (res.data.result === 200) {
              this.answerList();
              this.$message({type: "success", message: res.data.msg})
            } else {
              this.$message({type: "warning", message: res.data.msg})
            }
          }).catch(err => {
            console.log(err);
          })

        } else {
          this.$message('请登录后再评论');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //踩
      torubbish(aid, title, uid, username) {
        if (this.isLogin) {
          this.$post('index/data/rubbish', {aid, title, uid, username,}).then(res => {
            if (res.data.result === 200) {
              this.answerList();
              this.$message.success(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          })
        } else {
          this.$message('请登录后再操作');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //分享到微博
      toweibo(title, aid) {
        // let url=document.domain+'/essay?aid='+aid;
        let url = this.$httpUrl + '/template/quater_10_qkl/src/logo.png';
        let picurl = this.$httpUrl + '/template/quater_10_qkl/src/logo.png';
        var sharesinastring = 'http://v.t.sina.com.cn/share/share.php?title=' + title + '&url=' + url + '&content=utf-8&sourceUrl=' + url + '&pic=' + picurl;
        window.open(sharesinastring, 'newwindow', 'height=600,width=800,top=100,left=100');
      },
      //评论
      tocomment(index) {
        this.message = '';
        if (this.isLogin) {
          this.answer.map((value, index1, array) => {
            if (index1 === index) {
              this.answer[index].ablecomment = !this.answer[index].ablecomment;
              this.acindex = index;
              // this.answer=JSON.parse(JSON.stringify(this.answer));
              // this.answer=this.answer.concat();
              // this.answer=this.answer.slice();
              this.answer = Object.assign([], this.answer);
            } else {
              this.answer[index1].ablecomment = false;
            }
          })
        } else {
          this.$message('请登录后再评论');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //问题回答列表
      answerList() {
        let aid = this.aid;
        this.$post('index/data/question_answer_list', {aid}).then(res => {
          this.answer = res.data;
          this.totalnum = res.data.length;
          this.answer.map((val, index, arr) => {
            this.answer[index].head = this.$httpUrl + "/uc_server/avatar.php?uid=" + val.uid + "&amp;size=big";
            this.answer[index].list.map((value, index1, array) => {
              this.answer[index].list[index1].head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big";
            });
            val.ablecomment = false;
            val.commentwords = '';
            this.setqr()
          });
          this.answer = this.answer.slice();
          if (this.acindex !== '') {
            this.answer.map((value, index, array) => {
              if (this.acindex == index) {
                this.answer[index].ablecomment = true;
              }
            });
            this.answer = this.answer.slice();
          } else {
          }
        }).catch(err => {
          console.log(err);
        })
      },
      //获取邀请人员
      getinvite() {
        let aid = this.aid;
        this.$post('index/data/invite_question_list', {aid}).then(res => {
          this.invitelist = res.data;
          this.invitelist.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.invitelist = this.invitelist.slice();
        }).catch(err => {
          console.log(err);
        })
      },
      //文章收藏
      tocollect() {
        let art = this.art;
        let aid = art.aid;
        let title = art.title;
        let uid = art.uid;
        let username = art.username;
        if (this.isLogin) {
          this.$post('index/data/question_collect', {uid, username, aid, title}).then(res => {
            if (res.data.result === 200) {
              this.getart();
              this.$message({type: "success", message: res.data.msg})
            } else if (res.data.result === 201) {
              this.getart();
              this.$message({type: "success", message: res.data.msg})
            } else {
              this.$message({type: "warning", message: res.data.msg})
            }
          }).catch(err => {
            console.log(err);
          })
        } else {
          this.$message('请登录后再收藏');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //邀请
      toinvite(uid, username2) {
        if (this.isLogin) {
          let art = this.art;
          let aid = art.aid;
          let username = this.userInfo.username;
          let title = art.title;
          this.$post('index/data/invite_question', {aid, uid, username, username2, title}).then(res => {
            if (res.data.result === 200) {
              this.getinvite();
              this.$message({type: "success", message: res.data.msg})
            } else {
              this.$message({type: "warning", message: res.data.msg})
            }
          }).catch(err => {
            console.log(err);
          })

        } else {
          this.$message('请登录后再评论');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }


      },
      //保存为草稿
      save() {
        if (this.isLogin) {
          let art = this.art;
          let aid = art.aid;
          let catid = art.catid;
          let uid = art.uid;
          let content = this.result;
          let username = art.username;
          let subject = art.title;
          this.$post('index/data/answer_add', {
            aid,
            content,
            catid,
            uid,
            username,
            subject,
            status: 3
          }).then(res => {
            this.tips = '保存成功';
            if (res.data.result === 200) {
              this.$message({type: "success", message: res.data.msg})
            } else {
              this.$message({type: "warning", message: res.data.msg})
            }
          }).catch(err => {
            console.log(err);
          })
        } else {
          this.$message('请登录后再保存');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //提问回答
      submit() {
        if (this.isLogin) {
          //修改草稿
          if (this.draft == true) {
            let aid = this.art.aid;
            let uid = this.art.uid;
            let content = this.content;
            this.$post('index/data/draft', {aid, uid, content}).then(res => {
              if (res.data.result == 200) {
                this.$refs.wangeditor.clearHtml();
                this.$message.success("提交成功");
                this.answerList();
                // this.$message.success(res.data.msg);
              } else {
                this.$message.warning(res.data.msg)
              }
            }).catch(err => {
              console.log(err);
            })
          } else {
            let art = this.art;
            let aid = art.aid;
            let catid = art.catid;
            let uid = art.uid;
            let content = this.result;
            let username = art.username;
            let subject = art.title;
            this.$post('index/data/answer_add', {
              aid,
              content,
              catid,
              uid,
              username,
              subject,
            }).then(res => {
              if (res.data.result === 200) {
                this.result = '';
                this.content = '';
                this.$refs.wangeditor.clearHtml();
                this.answerList();
                this.$message({type: "success", message: res.data.msg})
              } else {
                this.$message({type: "warning", message: res.data.msg})
              }
            }).catch(err => {
              console.log(err);
            });
          }
        } else {
          this.$message('请登录后再评论');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },


      //发表评论
      toAnswer(index, aid, uid, username, catid, title) {
        let message = this.message;
        this.$post('index/data/comment', {aid, catid, title, message, uid, username}).then(res => {
          if (res.data.result === 200) {
            this.message = '';
            this.$message.success(res.data.msg);
            this.answer.map((value, index, array) => {
              if (value.id === aid) {
                this.answer[index].commentwords = '';
                // this.answer[index].comment.push(array)

              }
            });
            this.answerList();
            this.answer = this.answer.slice();
          } else {
            this.$message.error(res.data.msg);
          }
        }).catch(err => {
          console.log(err);
        })
      },
      setqr() {
        this.answer.map((value, index, arr) => {
          var id = value.id;
          let val = document.domain + "/essay?aid=" + value.aid;
          this.qr(val, id);
        })

      },
      qr(val, id) {
        var msg = document.getElementById(id);
        // 将获取到的数据（val）画到msg（canvas）上
        QRCode.toCanvas(msg, val, function (error) {
          // console.log(error)
        });


      },
      //获取富文本内容
      getcontent() {
        let aid = this.aid;
        let uid = this.$store.state.userInfo.uid;
        if (uid) {
          this.$post('index/data/draft', {aid, uid}).then(res => {
            this.content = res.data.data.content;
            if (res.data.data.content != null) {
              this.draft = true;
            }
          }).catch(err => {
            console.log(err);
          })
        }
      },
      //回答点赞
      topraise(id, title, uid, username) {
        // this.$post('index/data/like', {aid, title,uid,username}).then(res => {
        this.$post('index/data/comment_like', {id, title}).then(res => {
          if (res.data.result == 200) {
            this.$message.success(res.data.msg);
            this.answerList()
          } else {
            this.$message.warning(res.data.msg);
          }
        })
      }
    }
  }
</script>


<style scoped lang="scss">
  $main: #409eff !default;

  .essay {
    margin-top: 36px;
    display: flex;
    justify-content: space-between;

    .essay-left {
      width: 790px;

      .lables {
        margin-bottom: 30px;

        ul {
          display: flex;
          justify-content: flex-start;
          text-align: center;

          .label {
            padding: 0 9px;
            height: 28px;
            line-height: 28px;
            background: rgba(0, 150, 255, 1);
            border-radius: 14px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            margin-right: 22px;
            cursor: pointer;

            > a {
              color: rgba(255, 255, 255, 1);
            }
          }
        }
      }

      .article {


        .art-title {
          display: block;
          display: -webkit-box;
          font-size: 28px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 30px;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .summary {
          line-height: 32px;
          margin-top: 10px;
          font-size: 16px;
          color: #999;
        }

        .art-img {
          margin-top: 30px;
          margin-bottom: 40px;
          display: flex;

          img {
            margin-right: 10px;
            width: 190px;
            height: 124px;
          }
        }

        .btns {
          margin-top: 20px;
          display: flex;
          color: $main;

          > div {
            margin-right: 40px;
          }

          img {
            width: 15px;
            height: 15px;
            margin-right: 12px;
          }

          .weixin {
            img {
              height: 12px;
            }
          }

          .ans {
          }

          .collect {
            cursor: pointer;
          }

          .invite {
            cursor: pointer;
          }

          .share {
            display: flex;

            .share-way {
              margin-left: 26px;


              .weixin {
                position: relative;

                &:hover .qr-box {
                  display: flex;
                }

                .qr-box {
                  width: 94px;
                  height: 94px;
                  position: absolute;
                  left: 50%;
                  bottom: 25px;
                  transform: translateX(-50%);
                  display: none;
                  justify-content: center;
                  align-items: center;
                  background: rgba(255, 255, 255, 1);
                  box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
                  border-radius: 4px;

                  .qr {
                    width: 85px;
                    height: 85px;
                    border: 1px dashed #000;

                    > canvas {
                      width: 100% !important;
                      height: 100% !important;
                    }
                  }
                }
              }

              .weibo {
                cursor: pointer;
              }
            }
          }

        }
      }

      .invitebar {
        width: 100%;
        height: 380px;
        box-sizing: border-box;
        background: rgba(255, 255, 255, 1);
        border: 1px solid rgba(221, 221, 221, 1);
        border-radius: 10px;
        margin-top: 16px;
        margin-bottom: 25px;
        padding: 22px 40px 22px 33px;
        overflow-y: scroll;

        ul {
          flex-direction: column;

          .author {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 10px 0;

            .user {
              display: flex;
              align-items: center;
              cursor: pointer;

              .head {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 10px;

                img {
                  width: 100%;
                  height: 100%;
                  vertical-align: middle;
                }
              }

              .info {
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .name {
                  font-size: 16px;
                  font-weight: bold;
                  color: rgba(51, 51, 51, 1);
                  line-height: 25px;
                }

                .label {
                  font-size: 14px;
                  font-weight: 400;
                  color: rgba(102, 102, 102, 1);
                  line-height: 25px;
                }
              }

            }

            .btn {

            }
          }
        }
      }


      .editor {
        margin-top: 25px;
      }

      .submit {
        display: flex;
        justify-content: space-between;
        margin-top: 15px;

        .tips {
          color: #808080;
          font-size: 14px;
        }
      }

      .response {
        margin-top: 27px;

        .response-num {
          color: $main;
          position: relative;
          font-weight: 400;
          line-height: 40px;
          text-align: center;
          display: inline-block;

          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 4px;
            background: $main;
            border-radius: 2px;
          }
        }
      }

      .answer {
        width: 100%;
        margin-bottom: 10px;

        > ul {
          display: flex;
          flex-direction: column;

          .ans-item {
            .user {
              display: flex;
              height: 84px;
              align-items: center;
              cursor: pointer;

              .head {
                width: 40px;
                height: 40px;
                overflow: hidden;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                img {
                  width: 100%;
                  height: 100%;
                }
              }

              .user-info {
                margin-left: 15px;

                .top {
                  display: flex;

                  .name {
                    cursor: pointer;
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 28px;
                    margin-right: 10px;
                  }

                  .labels {
                    font-size: 14px;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 28px;
                  }
                }

                .bottom {
                  font-size: 12px;
                  font-weight: 400;
                  color: rgba(153, 153, 153, 1);
                  line-height: 28px;
                }
              }
            }

            .operate {
              margin-top: 10px;
              font-size: 14px;
              font-weight: 400;
              color: #999999;
              line-height: 20px;
              height: 30px;
              display: flex;
              align-items: center;
              justify-content: space-between;

              .left {
                display: flex;
                justify-content: flex-start;
              }

              .exist {
                color: $main;
              }

              span {
                margin-right: 30px;
              }

              .praise, .tread {
                &:hover {
                  color: #cacaca;
                }

                cursor: pointer;
              }

              .comment {
                cursor: pointer;

                &:hover {
                  color: #cacaca;
                }
              }

              .share {
                display: flex;
                justify-content: flex-start;

                > img {
                  width: 15px;
                  height: 15px;
                }

                &:hover {
                  color: #cacaca;
                }

                .share-way {
                  margin-left: 26px;


                  .weixin {
                    position: relative;

                    &:hover .qr-box {
                      display: flex;
                    }

                    .qr-box {
                      width: 94px;
                      height: 94px;
                      position: absolute;
                      left: 50%;
                      bottom: 20px;
                      transform: translateX(-50%);
                      display: none;
                      justify-content: center;
                      align-items: center;
                      background: rgba(255, 255, 255, 1);
                      box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
                      border-radius: 4px;

                      .qr {
                        width: 85px;
                        height: 85px;
                        border: 1px dashed #000;

                        > canvas {
                          width: 100% !important;
                          height: 100% !important;
                        }
                      }
                    }
                  }

                  .weibo {
                    cursor: pointer;
                  }
                }
              }

              img {
                width: 15px;
                vertical-align: middle;
                margin-right: 5px;
              }
            }

            .comments {
              margin-top: 30px;
              background: rgba(255, 255, 255, 1);
              box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
              border-radius: 0px 6px 6px 6px;
              -webkit-box-sizing: border-box;
              -moz-box-sizing: border-box;
              box-sizing: border-box;
              padding: 30px 44px 15px 34px;

              .sendword {
                -webkit-box-sizing: border-box;
                -moz-box-sizing: border-box;
                box-sizing: border-box;

                .text {
                  padding: 30px 0 15px 0;
                  width: 712px;
                  height: 70px;

                  > textarea {
                    width: 100%;
                    height: 100%;
                    outline: none;
                    background: rgba(248, 248, 248, 1);
                    border-radius: 4px;
                    padding-left: 15px;
                    padding-top: 5px;
                    box-sizing: border-box;
                  }
                }

                .btn {
                  display: flex;
                  justify-content: flex-end;
                  padding-bottom: 15px;
                }
              }

              .comment-list {
                ul > {
                  .list {
                    display: flex;
                    flex-direction: column;
                    padding: 5px 0;
                    box-sizing: border-box;

                    .box {
                      display: flex;

                      .head {
                        cursor: pointer;
                        width: 50px;
                        height: 50px;
                        overflow: hidden;
                        border-radius: 50%;
                        margin-right: 20px;

                        > img {
                          width: 100%;
                          height: 100%;
                        }
                      }

                      .info {
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .top {
                          display: flex;
                          justify-content: space-between;

                          .name {
                            font-size: 14px;
                            font-weight: bold;
                            color: rgba(51, 51, 51, 1);
                            line-height: 18px;
                            cursor: pointer;
                          }

                          .date {
                            font-size: 14px;
                            font-weight: 400;
                            color: rgba(153, 153, 153, 1);
                            line-height: 18px;
                          }
                        }

                        .comword {
                          width: 550px;
                          margin-top: 9px;
                          font-size: 14px;
                          font-weight: 400;
                          color: rgba(51, 51, 51, 1);
                          line-height: 18px;
                        }

                        .opt {
                          font-size: 14px;
                          font-weight: 400;
                          color: #999999;
                          line-height: 20px;
                          height: 30px;
                          display: flex;
                          align-items: center;

                          span {
                            margin-right: 30px;

                          }

                          .comment {
                            cursor: pointer;
                          }

                          .praise, .tread {
                            cursor: pointer;
                          }

                          img {
                            width: 15px;
                            vertical-align: middle;
                            margin-right: 5px;
                          }
                        }
                      }
                    }

                    .seccomment {
                      margin-top: 10px;
                      display: flex;
                      justify-content: space-between;
                      width: 713px;
                      height: 84px;
                      background: rgba(255, 255, 255, 1);
                      border: 1px solid rgba(220, 220, 220, 1);
                      border-radius: 6px;

                      .sec-box {
                        padding: 13px;
                        box-sizing: border-box;

                        > textarea {
                          padding-left: 10px;
                          box-sizing: border-box;
                          padding-top: 5px;
                          width: 596px;
                          height: 58px;
                          background: rgba(248, 248, 248, 1);
                          border-radius: 6px;
                        }
                      }

                      .respond {

                        padding: 13px 24px 13px 0;

                        .resbtn {
                          width: 58px;
                          height: 58px;
                          box-sizing: border-box;
                          background: rgba(0, 150, 255, 1);
                          box-shadow: 0px 2px 6px 0px rgba(0, 150, 255, .5);
                          border-radius: 6px;
                        }

                        /deep/ .el-button {
                          display: flex;
                          justify-content: center;
                          color: #fff;
                        }
                      }
                    }


                  }
                }
              }
            }
          }
        }
      }

    }

    .essay-right {
      width: 350px;
    }


  }

  /*评论列表*/
  .lists {
    width: 100%;
    padding-bottom: 20px;

    .box {
      clear: both;

      .sofa {
        width: 90%;
        margin: auto;
        align-items: center;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(222, 222, 222, .6);

        .texts {
          resize: none;
          outline: none;
          height: 100px;
          width: 100%;
          display: none;
        }

        .sofa-inl {
          display: flex;

          .head-pic {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            margin: 25px 0;
          }

          .comment-con {
            width: 627px;
            margin-left: 25px;
            padding-bottom: 10px;
            margin-top: 20px;

            .member {
              font-size: 14px;
              display: flex;
              height: 24px;
              line-height: 20px;
              text-align: left;
              font-weight: bold;

              p {
                width: 100%;
                font-size: 14px;
                font-weight: bold;
                height: 24px;

                span {
                  float: right;
                  color: #999999;
                  font-size: 14px;
                }
              }

            }

            .text {
              width: 100%;
              font-size: 14px;
              color: #333333;
            }

            .interact {
              height: 20px;
              font-size: 14px;
              color: #999999;

              > .mg {
                cursor: pointer;
                margin-left: 20px;
                margin-right: 5px;
              }
            }

            .diss {
              display: flex;
              margin-top: 10px;
              background: #d9f2ff;
              border-radius: 5px;
              margin-bottom: 10px;
              align-items: center;

              .head-pic2 {
                width: 34px;
                height: 34px;
                margin: 38px 10px;
                border-radius: 50%;
              }

              .comment-con2 {
                width: 553px;

                .member2 {
                  height: 30px;
                  display: flex;
                  margin: 10px 0;
                  font-size: 14px;
                  line-height: 30px;

                  .time {
                    width: 240px;
                    text-align: right;
                  }

                  .top_member {
                    width: 296px;
                    background: #c3ebff;
                    border-radius: 5px;
                    text-indent: 10px;
                    display: flex;
                  }
                }

                .text2 {
                  height: 20px;
                  display: flex;
                  font-size: 14px;
                  text-indent: 10px;
                  margin: 12px 0;
                  align-items: center;

                  p {
                    color: #333333;
                    font-weight: bold;
                  }

                  span {
                    color: #333333;
                    width: 360px;
                    overflow: hidden;
                    font-size: 14px;
                  }
                }

                .interact2 {
                  font-size: 14px;
                  color: #355ec7;
                  display: flex;

                  img {
                    margin: 8px 5px;
                    width: 12px;
                    height: 12px;

                  }
                }

                .member3 {
                  display: flex;
                  width: 500px;
                  height: 40px;
                  background: #c3ebff;
                  border-radius: 4px;
                  line-height: 40px;
                  margin-top: 10px;
                  margin-bottom: 10px;
                  font-size: 14px;
                  text-indent: 20px;

                  p {
                    font-weight: bold;
                    line-height: 40px;
                  }

                  span {
                    width: 280px;
                    overflow: hidden;
                  }
                }
              }
            }
          }
        }

        .sofa-box {
          width: 100%;
          height: 84px;
          border-radius: 6px;
          padding-left: 40px;
          box-sizing: border-box;
          display: flex;
          align-items: center;
          margin-bottom: 8px;

          .sofa-imgs {
            margin-right: 15px;
            margin-left: 20px;
          }

          .sofa-text {
            width: 558px;
            height: 58px;
            background: #F8F8F8;
            border-radius: 6px;
            resize: none;
            outline: none;
            border: none;
            padding-top: 10px;
            padding-left: 10px;
            box-sizing: border-box;
          }

          .sofa-btn {
            width: 70px;
            height: 58px;
            background: rgba(53, 94, 199, 1);
            border-radius: 6px;
            font-size: 14px;
            color: #ffffff;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 15px;
            margin-left: 20px;
            margin-right: 20px;
          }

          .sofa-btn span {
            width: 29px;
            height: 33px;
            cursor: pointer;
          }
        }

        .reply-three {
          width: 632px;
          height: 84px;
          border-radius: 6px;
          margin-left: 70px;
          display: flex;
          justify-content: space-between;
          align-items: center;

          .reply-img {
            margin-left: 20px;
            margin-right: 20px;

            img {
              border-radius: 50%;
            }
          }

          .time {
            color: #999999;
            font-size: 14px;
          }

          .reply-center {
            flex: 1;
          }

          .reply-box1 {
            margin-bottom: 10px;

            span:nth-child(1) {
              color: #333333;
              font-size: 14px;
              font-weight: bold;
            }

            span:nth-child(2) {
              color: #333333;
              font-size: 14px;
            }

            span:nth-child(3) {
              color: #333333;
              font-size: 14px;
            }
          }
        }
      }
    }


  }

</style>



